<template>
  <VanPopup
    style="
      box-sizing: border-box;
      width: 93%;
      background-color: #1f1c1d;
      padding: 5px;
      border-radius: 10px;
    "
    v-model="show"
    @click-overlay="closePop"
  >
    <div class="wrap">
      <div class="header">
        <div class="name">{{ taskDetail.title }}</div>
        <div class="reward">
          {{ $t('admin.take')
          }}<span>{{ +taskDetail.times * +taskDetail.award || 0 }}</span> SFO
        </div>
      </div>
      <div class="content">
        <div class="detail">
          <div class="name">{{ $t('admin.detail') }} :</div>
          <div class="text">{{ taskDetail.content || 0 }}</div>
        </div>
        <div class="key-value">
          <div class="key">{{ $t('admin.reward') }}</div>
          <div class="value" translate="no">
            {{ taskDetail.times * taskDetail.award }}
            {{ tokenList[taskDetail.tokenType] || '' }}
          </div>
        </div>
        <div class="key-value">
          <div class="key">{{ $t('admin.number') }}</div>
          <div class="value" translate="no">
            {{ taskDetail.times || 0 }}{{ $t('common.servings') }}
          </div>
        </div>
        <div class="key-value">
          <div class="key">{{ $t('admin.validTime') }}</div>
          <div class="value" translate="no">
            {{ taskDetail.taskDuration / 60 / 24 }}{{ $t('common.day') }}
          </div>
        </div>
        <div class="key-value">
          <div class="key">{{ $t('admin.peddingReview') }}</div>
          <div class="value" translate="no">
            {{ taskDetail.reviewing || 0 }}{{ $t('common.servings') }}
          </div>
        </div>
        <div class="key-value">
          <div class="key">{{ $t('admin.rejected') }}</div>
          <div class="value" translate="no">
            {{ taskDetail.rejected || 0 }}{{ $t('common.servings') }}
          </div>
        </div>
        <div class="key-value">
          <div class="key">{{ $t('admin.passed') }}</div>
          <div class="value" translate="no">
            {{ taskDetail.passed || 0 }}{{ $t('common.servings') }}
          </div>
        </div>
      </div>
      <div class="button-group">
        <div class="button-item" @click="handleToReviewList">
          {{ $t('admin.goReview') }}
        </div>
      </div>
    </div>
  </VanPopup>
</template>

<script>
import { handleTaskDetail } from '@/services/task'
export default {
  props: ['tempData'],
  name: '',
  data () {
    return {
      show: true,
      taskDetail: {},
      tokenList: []
    }
  },
  methods: {
    closePop () {
      this.$emit('close')
    },
    async getDetail () {
      const resp = await handleTaskDetail(this.tempData.id)
      if (!resp.success) return
      this.taskDetail = resp.data
    },
    handleToReviewList () {
      console.log('this.tempData.taskID', this.tempData)
      this.$router.push(`/ReviewList?taskId=${this.tempData.id}`)
    }
  },
  mounted () {
    this.getDetail()
    this.tokenList = JSON.parse(localStorage.getItem('tokenList'))
  }
}
</script>

