<template>
  <div class="task-item-wrap">
    <div class="header-wrap">
      <div class="status1" v-if="dataSource.status === 0">
        {{ $t('admin.reviewing') }}
      </div>
      <div class="status2" v-if="dataSource.status === 2">
        {{ $t('admin.reviewFail') }}
      </div>
      <div class="status2" v-if="dataSource.status === 3">
        {{ $t('admin.refunded') }}
      </div>
      <div class="header">
        <div class="task">
          <div class="name">{{ dataSource.title }}</div>
        </div>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#xingxingbeifen"></use>
        </svg>
        <div class="time">
          <div class="des">{{ $t('admin.validTime') }}</div>
          <div class="time" translate="no">
            {{ dataSource.taskDuration / 60 / 24 }}{{ $t('common.day') }}
          </div>
        </div>
      </div>
    </div>
    <div class="content" translate="no">
      <div class="reward">
        {{ $t('admin.reward') }}
        <span>{{ parseInt(dataSource.award) }} {{ tokenName }}</span>
      </div>
      <div class="copies">
        {{ $t('admin.number') }}
        <span
          >{{ dataSource.times - dataSource.completionsTimes }}/{{
            dataSource.times
          }}</span
        >
      </div>
      <div class="button" @click="handleShowDetail">
        {{ $t('admin.detail') }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  props: ['dataSource'],
  data () {
    return {
      tokenName: ''
    }
  },
  methods: {
    handleShowDetail () {
      this.$emit('show', this.dataSource)
    }
  },
  mounted () {
    this.tokenName = JSON.parse(localStorage.getItem('tokenList'))[this.dataSource.tokenType]
  }
}
</script>

