<template>
  <div class="agency-wrap">
    <AgencyHeader></AgencyHeader>
    <div id="content">
      <div class="name">
        <span>{{ $t('admin.taskAdmin') }}</span>
        <svg
          class="icon"
          aria-hidden="true"
          @click="$gbUtils.handleToPage('/releaseTask')"
        >
          <use xlink:href="#fabu"></use>
        </svg>
      </div>
      <template v-if="taskList.length > 0">
        <TaskItem
          @show="handleShowDetail"
          v-for="item in taskList"
          :key="item.id"
          :dataSource="item"
        ></TaskItem>
      </template>
      <div class="no-data" v-else>{{ $t('common.noData') }}</div>
      <TaskDetailPop
        v-if="detailShow"
        :tempData="tempData"
        @close="handleShowDetail"
      ></TaskDetailPop>
    </div>
  </div>
</template>

<script>
import { handleAdminList } from '@/services/task'
import TaskDetailPop from './components/TaskDetailPop.vue'
import AgencyHeader from './components/AgencyHeader.vue'
import TaskItem from './components/TaskItem.vue'
export default {
  name: '',
  data () {
    return {
      detailShow: false,
      taskList: [],
      tempData: {}
    }
  },
  methods: {
    handleShowDetail (data) {
      this.detailShow = !this.detailShow
      this.tempData = data
    },
    async getList () {
      const resp = await handleAdminList({
        orderByColumn: 'id',
        isAsc: 'desc'
      })
      if (!resp.success) return
      this.taskList = resp.rows
      console.log('resp', resp)
    }
  },
  mounted () {
    this.getList()
  },
  components: { AgencyHeader, TaskItem, TaskDetailPop }
}
</script>

